import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IndexPage } from './index.page';

const routes: Routes = [
 
  {
    path: '', 
    loadChildren: () => import('../home/college-predictor/college-predictor.module').then( m => m.CollegePredictorPageModule)
  },
  {
    path: 'login', 
    loadChildren: () => import('./login/login.module').then( m => m.LoginPageModule)
  },
  {
    path: 'register',
    loadChildren: () => import('./register/register.module').then( m => m.RegisterPageModule)
  },
  {
    path: 'forgot-password',
    loadChildren: () => import('./forgot-password/forgot-password.module').then( m => m.ForgotPasswordPageModule)
  },
  {
    path: 'otp-verification',
    loadChildren: () => import('./otp-verification/otp-verification.module').then( m => m.OtpVerificationPageModule)
  },
  {
    path: 'login-new',
    loadChildren: () => import('./login-new/login-new.module').then( m => m.LoginNewPageModule)
  },
  {
    path: 'otp-verification-new',
    loadChildren: () => import('./otp-verification-new/otp-verification-new.module').then( m => m.OtpVerificationNewPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class IndexPageRoutingModule {}
